ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333333;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #111111;
}

nav {
  margin: auto;
  width: 60vw;
  min-width: 300px;
}

.search-box {
  float: right;
}

.page-link {
  background: none !important;
  border: none;
  padding: 16px;
  cursor: pointer;
  color: white;
  outline: none;
}
